body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.App-header {
  background: linear-gradient(
                     rgba(0, 57, 245, 0.836), 
                     rgba(2, 30, 156, 0.651));
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  
}

.quote-author{
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  font-weight: 600;
  z-index: 1;
  font-style: italic;
  color: #045;

}
.quoteText{
  font-family: 'Raleway', sans-serif;
  text-align: center;
  color: #045;
  width: 700px;
 }

.card-action{
  position: unset!important;
  
}

.share_button{
  float: left;
  color: teal;
}

#quoteButton{
  width:200px;
	margin-top: 10px;
	border:2px solid teal;
	color:#045;
	font-family: inherit;
	font-weight: bold;
	padding:5px;
	text-decoration: none;
	text-align: center;
}

#quoteButton:hover{
	cursor:pointer;
	background:#045;
	color: #fff;	
}
#quoteButton:active{
	cursor: pointer;
}
.titleQuote{
  font-family: 'Raleway', sans-serif;
  font-size: 40px;
  font-weight: 600;
  color: white;
}
img{
  width: 80vw;
}
